.redux-main .redux-media-slider {
	width: 40%;
	display: inline-block;
	margin-left: 30px;
}
.redux-main .redux-media-filter-container {
	padding-top: 20px;
}
.redux-main .redux-media-filter-container .container-label {
	margin-bottom: 20px;
	padding-bottom: 1px;
	border-bottom: 1px solid #e7e7e7;
	font-weight: 600;
	font-size: 12px;
	color: #999;
}
.redux-main .redux-media-filter-container .media-filter {
	display: inline-block;
	width: 47%;
	margin-bottom: 5px;
}
.redux-main .redux-media-filter-container .media-filter label {
	display: inline-block;
	width: 130px;
	color: #999;
}
.redux-main .redux-media-filter-container .media-filter label.disabled .filter-value {
	color: #ccc;
}
